<template>
  <v-dialog v-model="showPopup" persistent width="550">
    <v-card style="text-align: left;">
      <v-card-text style="text-align: center;">
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="12">
            <v-img
              :src="require('@/assets/image/divider-small.png')"
              contain
              height="14"
              class="mt-4"
            >
            </v-img>
          </v-col>
        </v-row>
        <v-row class="pa-0 ma-0" align="center" justify="center">
          <v-col cols="12">
            <p class="p-title p-bold">
              마음 전하실 곳
            </p>
            <v-divider> </v-divider>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-text class="px-6 py-0" v-show="bankData.my != ''">
        <v-row class="pa-0 ma-0">
          <v-col cols="12" class="pa-1">
            <p class="p-subtitle ma-0"><span :class="target == 'groom' ? 'span-groom' : 'span-bride'">{{target == "groom" ? '신랑' : '신부'}}</span> {{name.my}}</p>
          </v-col>
          <v-col class="px-2">
            <p class="p-subtitle">{{bankData.my}} ·<v-btn class="pa-0 ml-9" plain @click="copyClipboard('my')"><v-icon dense small class="mr-1">mdi-content-copy</v-icon>복사하기</v-btn><br/> {{accountNumberData.my}}</p>
            <v-divider> </v-divider>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-text class="px-6 py-0" v-show="bankData.father != ''">
        <v-row class="pa-0 ma-0">
          <v-col cols="12" class="pa-1">
            <p class="p-subtitle ma-0">아버지 {{name.father}}</p>
          </v-col>
          <v-col class="px-2">
            <p class="p-subtitle">{{bankData.father}} ·<v-btn class="pa-0 ml-9" plain @click="copyClipboard('father')"><v-icon dense small class="mr-1">mdi-content-copy</v-icon>복사하기</v-btn><br/> {{accountNumberData.father}}</p>
            <v-divider> </v-divider>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-text class="px-6 py-0" v-show="bankData.mother != ''">
        <v-row class="pa-0 ma-0">
          <v-col cols="12" class="pa-1">
            <p class="p-subtitle ma-0">어머니 {{name.mother}}</p>
          </v-col>
          <v-col class="px-2">
            <p class="p-subtitle">{{bankData.mother}} ·<v-btn class="pa-0 ml-9" plain @click="copyClipboard('mother')"><v-icon dense small class="mr-1">mdi-content-copy</v-icon>복사하기</v-btn><br/> {{accountNumberData.mother}}</p>
            <v-divider> </v-divider>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="green darken-1" text @click="hidePopup"
          >닫기</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

async function copyAccountNumber(text) {
  try {
    await navigator.clipboard.writeText(text);
    alert('계좌번호를 복사했습니다.');
  } catch (err) {
    alert('Failed to copy: ' + err);
  }
}

export default {
  name: "AccountNumber",
  components: {

  },
  props: {
    showPopup: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
  methods: {
    hidePopup() {
      this.$emit("hidePopup")
    },
    clear() {
      this.target = ""
      this.bankData = {
        father: "",
        mother: "",
        my: ""
      }
      this.accountNumberData = {
        father: "",
        mother: "",
        my: ""
      }
    },
    setAccountNumberData(target) {
      if (target != null) {
        this.target = target
        this.name = this.accountNumbers[target].name
        this.bankData = this.accountNumbers[target].bankData
        this.accountNumberData = this.accountNumbers[target].accountNumberData
      } else {
        this.clear()
      }
    },
    copyClipboard(target) {
      var text = this.bankData[target] + " " + this.accountNumberData[target]
      copyAccountNumber(text)
      // window.navigator.clipboard.writeText(text)
      //   .then(() => {alert("계좌번호를 복사했습니다.")})
      //   .catch((error) => {alert(error)})
    },
    copyClipboardForMobile(text) {
      const copyTextArea = document.createElement("textarea")
      copyTextArea.setAttribute("style","position:absolute; right:200%;")
      copyTextArea.textContent = text
      document.body.appendChild(copyTextArea)
      copyTextArea.select()
      copyTextArea.setSelectionRange(0, 99999) /*For mobile devices*/
      document.execCommand("copy")
      copyTextArea.remove()
      alert("copyClipboardForMobile " + text)
    },
  },
  data: () => ({
    target: "groom",
    name: {
      father: "",
      mother: "",
      my: ""
    },
    bankData: {
      father: "",
      mother: "",
      my: ""
    },
    accountNumberData: {
      father: "",
      mother: "",
      my: ""
    },
    accountNumbers: {
      groom: {
        name: {
          father: "",
          mother: "오영임",
          my: "이동찬"
        },
        bankData: {
          father: "",
          mother: "우리은행",
          my: "신한은행"
        },
        accountNumberData: {
          father: "",
          mother: "1002-851-616472",
          my: "110-518-608680"
        }
      },
      bride: {
        name: {
          father: "고문석 · 필립 보",
          mother: "안신자 · 프란체스카",
          my: "고혜영 · 마리아 막달레나"
        },
        bankData: {
          father: "국민은행",
          mother: "하나은행",
          my: "신한은행"
        },
        accountNumberData: {
          father: "846210115764",
          mother: "203-910065-82107",
          my: "110-468-172942"
        }
      },
    }
  }),
};
</script>

