<!--포토 갤러리-->
<template>
  <div class="pa-0 ma-0" style="">
    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <v-col class="pa-0">
        <p class="p-title" style="">사진첩</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0">
      <v-col>
        <p class="mb-4 p-subtitle" style="">저희의 사랑을 담은 순간</p>
      </v-col>
    </v-row>

    <v-row class="pa-0 ma-0 text-center" align="center" justify="center">
      <!-- 메인 포토 -->
      <!-- <v-col cols="12" class="">
        <v-carousel 
          hide-delimiters
          show-arrows-on-hover
          :height="photoHeightHor"
          v-model="selected"
          cycle
          interval="10000"
        >
          <v-carousel-item
            v-for="(item, i) in photoItemsHor"
            :key="i"
            :src="item.src"
          ></v-carousel-item>
        </v-carousel>
      </v-col> -->
      
      <!-- 미리보기 -->
      <v-col cols="12" class="pt-2">
        <v-container class="pa-0">
          <v-item-group
            v-model="selected"
          >
            <v-row 
              class="pa-0 ma-0"
              v-for="(photoItem, i) in photoItems"
              :key="'photoItem_' + i"
              v-show="photoItem.show == true"
            >
              <v-col
                v-for="(item, j) in photoItem.item"
                :key="j"
                cols="3"
                md="3"
              >
                <v-hover v-slot="{ hover }">
                  <v-item v-slot="{ active, toggle }" :class="{ 'on-hover': hover }">
                    <v-img
                      :src="item.src"
                      class="text-right pa-2"
                      :style="active ? 'border: 2px solid #CCC; opacity: 1 !important;' : ''"
                      @click="toggle"
                    >
                    </v-img>
                  </v-item>
                </v-hover>
              </v-col>
            </v-row>
            <v-col cols="12" v-show="!allPhotoShow">
              <v-row class="pa-0 ma-0" align="center" justify="center">
                <v-col cols="6" style="border: 1px solid #BBB; background-color: rgba(0, 0, 0, 0); cursor: pointer;" @click="showMorePhoto()">
                  <span style="font-size: .9rem;"><v-icon dense class="mr-3">mdi-arrow-down-drop-circle-outline</v-icon>사진 더보기</span>
                </v-col>
              </v-row>
            </v-col>
          </v-item-group>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>

